@use 'base/variables' as v;
@use 'base/mixins' as m;

.paginacion{
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    @include m.mediaq(v.$tablet){
        justify-content: flex-end;
    }

    &__enlace{
        color: v.$azul;
        font-size: 1.6rem;

        &--texto{
            padding: 1rem;
            border-radius: .5rem;
            border: 1px solid v.$azul;

            &:hover{
                background-color: v.$azul;
                color: v.$blanco;
            }
        }

        &--numero{
            font-weight: v.$bold;
            display:none;
            @include m.mediaq(v.$tablet){
                display:inline;
            }
        }

        &--activo{
            color:darken(v.$azul, 35%);
            font-weight: v.$bold;
            display:none;
            @include m.mediaq(v.$tablet){
                display:inline;
            }
        }

    }
}