@use 'base/variables' as v;

.acciones{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
    
    &--centrar{
        @extend .acciones;
        justify-content: center;
    }
    

    &__enlace{
        color:v.$negro;
        font-size: 1.4rem;
        font-weight: v.$delgada;    

        &:hover{
            font-weight: v.$bold;
        }
    }   

    




     
}


